</template>

<script>
import axios from 'axios';
import Loader from '../components/Loader';
import { getClasses } from '../utils/api/classes';
import { getCareers } from '../utils/api/career';
import { getJobs } from '../utils/api/jobs';
import { getUsers } from '../utils/api/users';
import { getQuizzes } from '../utils/api/quiz';
import { getAlumni } from '../utils/api/alumni';
import { getBadges } from '../utils/api/badge';

export default {
	name: 'Overview',
	components: {
		Loader
	},
	data() {
		return {
			users: {},
			jobs: {},
			classes: {},
			quiz: {},
			alumni: {},
			badges: {},
			careers: {},
			skillset: {},
			loading: false,
			loadingStats: true,
			statsLoaded: false
		};
	},
	created() {
		this.getStats();
	},
	methods: {
		getStats() {
			axios.all([
				getJobs(),
				getClasses(),
				getUsers(),
				getQuizzes(),
				getAlumni(),
				getBadges(),
				getCareers()
			])
				.then(axios.spread(
					(jobs, classes, users, quiz, alumni, badges, careers) => {
						this.jobs = jobs.data;
						this.classes = classes.data;
						this.users = users.data;
						this.quiz = quiz.data;
						this.alumni = alumni.data;
						this.badges = badges.data;
						this.careers = careers.data;
						this.statsLoaded = true;
					}
				))
				.catch((error) => {
					this.statsLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingStats = false;
				});
			return true;
		}
	}
};
</script>

<style lang="scss" scoped>
.loading {
	min-height: 80vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
</style>
